import { Component, Input, Output, EventEmitter, OnInit } from '@angular/core';
import { CommonModule } from '@angular/common';

export interface ConfirmationModalData {
  title: string;
  message: string;
  confirmText: string;
  cancelText: string;
  type: 'delete' | 'warning' | 'info';
}

@Component({
  selector: 'app-confirmation-modal',
  standalone: true,
  imports: [CommonModule],
  template: `
    <div class="modal-overlay" *ngIf="isVisible" (click)="onOverlayClick($event)">
      <div class="modal-container" [dir]="isRTL ? 'rtl' : 'ltr'">
        <!-- Close Button -->
        <button class="close-btn" (click)="onCancel()">
          <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2">
            <line x1="18" y1="6" x2="6" y2="18"></line>
            <line x1="6" y1="6" x2="18" y2="18"></line>
          </svg>
        </button>

        <!-- Icon -->
        <div class="modal-icon" [class]="'icon-' + data.type">
          <svg *ngIf="data.type === 'delete'" width="48" height="48" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2">
            <polyline points="3,6 5,6 21,6"></polyline>
            <path d="M19,6v14a2,2,0,0,1-2,2H7a2,2,0,0,1-2-2V6m3,0V4a2,2,0,0,1,2-2h4a2,2,0,0,1,2,2V6"></path>
            <line x1="10" y1="11" x2="10" y2="17"></line>
            <line x1="14" y1="11" x2="14" y2="17"></line>
          </svg>
          <svg *ngIf="data.type === 'warning'" width="48" height="48" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2">
            <path d="M10.29 3.86L1.82 18a2 2 0 0 0 1.71 3h16.94a2 2 0 0 0 1.71-3L13.71 3.86a2 2 0 0 0-3.42 0z"></path>
            <line x1="12" y1="9" x2="12" y2="13"></line>
            <line x1="12" y1="17" x2="12.01" y2="17"></line>
          </svg>
          <svg *ngIf="data.type === 'info'" width="48" height="48" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2">
            <circle cx="12" cy="12" r="10"></circle>
            <line x1="12" y1="16" x2="12" y2="12"></line>
            <line x1="12" y1="8" x2="12.01" y2="8"></line>
          </svg>
        </div>

        <!-- Title -->
        <h2 class="modal-title">{{ data.title || 'No Title' }}</h2>

        <!-- Message -->
        <p class="modal-message">{{ data.message || 'No Message' }}</p>

        <!-- Action Buttons -->
        <div class="modal-actions">
          <button class="btn btn-confirm"             [class]="'btn-' + data.type" (click)="onConfirm()">
            {{ data.confirmText || 'Confirm' }}
          </button>
        </div>
      </div>
    </div>
  `,
  styles: [`
    .modal-overlay {
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: rgba(0, 0, 0, 0.5);
      backdrop-filter: blur(4px);
      display: flex;
      align-items: center;
      justify-content: center;
      z-index: 99999;
      padding: 1rem;
    }

    .modal-container {
      background: white;
      border-radius: 12px;
      padding: 2rem;
      max-width: 400px;
      width: 100%;
      position: relative;
      box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
      animation: modalSlideIn 0.3s ease-out;
      justify-content: center !important;
    }

    @keyframes modalSlideIn {
      from {
        opacity: 0;
        transform: scale(0.95) translateY(-10px);
      }
      to {
        opacity: 1;
        transform: scale(1) translateY(0);
      }
    }

    .close-btn {
      position: absolute;
      top: 1rem;
      left: 1rem;
      background: none;
      border: none;
      color: #6b7280;
      cursor: pointer;
      padding: 0.5rem;
      border-radius: 6px;
      transition: all 0.2s;
      display: flex;
      align-items: center;
      justify-content: center;
    }

    .close-btn:hover {
      background: #f3f4f6;
      color: #374151;
    }

    .modal-icon {
      text-align: center;
      margin-bottom: 1.5rem;
    }

    .icon-delete {
      color: #dc2626;
    }

    .icon-warning {
      color: #f59e0b;
    }

    .icon-info {
      color: #3b82f6;
    }

    .modal-title {
      font-size: 1.5rem;
      font-weight: 700;
      color: #111827;
      text-align: center !important;
      margin-bottom: 1rem;
      line-height: 1.4;
    }

    .modal-message {
      color: #6b7280;
      text-align: center;
      margin-bottom: 2rem;
      line-height: 1.6;
      font-size: 1rem;
      justify-content: center !important;
    }

    .modal-actions {
      display: flex;
      gap: 1rem;
      justify-content: center;
    }

    .btn {
      padding: 0.75rem 1.5rem;
      border-radius: 8px;
      font-weight: 600;
      font-size: 0.875rem;
      cursor: pointer;
      transition: all 0.2s;
      border: none;
      min-width: 100px;
    }

    .btn-cancel {
      background: white;
      color: #374151;
      border: 2px solid #e5e7eb;
    }

    .btn-cancel:hover {
      background: #f9fafb;
      border-color: #d1d5db;
    }

    .btn-confirm {
      color: white;
    }

    .btn-delete {
      background: #dc2626;
    }

    .btn-delete:hover {
      background: #b91c1c;
    }

    .btn-warning {
      background: #f59e0b;
    }

    .btn-warning:hover {
      background: #d97706;
    }

    .btn-info {
      background: #3b82f6;
    }

    .btn-info:hover {
      background: #2563eb;
    }

    /* RTL Support */
    .modal-container[dir="rtl"] .close-btn {
      left: auto;
      right: 1rem;
    }

    .modal-container[dir="rtl"] .modal-title,
    .modal-container[dir="rtl"] .modal-message {
      text-align: center;
      justify-content: center;
    }

    /* Responsive */
    @media (max-width: 480px) {
      .modal-container {
        padding: 1.5rem;
        margin: 1rem;
      }

      .modal-actions {
        flex-direction: column;
      }

      .btn {
        width: 100%;
      }
    }
  `]
})
export class ConfirmationModalComponent {
  @Input() data!: ConfirmationModalData;
  @Input() isRTL = false;
  @Input() isVisible = false;
  @Output() confirm = new EventEmitter<void>();
  @Output() cancel = new EventEmitter<void>();

  ngOnInit() {
    console.log('ConfirmationModal initialized with data:', this.data);
    console.log('ConfirmationModal isVisible:', this.isVisible);
  }

  show(data: ConfirmationModalData): void {
    this.data = data;
    this.isVisible = true;
  }

  hide(): void {
    this.isVisible = false;
  }

  onConfirm(): void {
    this.confirm.emit();
    this.hide();
  }

  onCancel(): void {
    this.cancel.emit();
    this.hide();
  }

  onOverlayClick(event: Event): void {
    if (event.target === event.currentTarget) {
      this.onCancel();
    }
  }
}
